.under_header {
  @apply w-full h-[776px] relative bg-white bg-opacity-0 border-b border-stone-50 border-opacity-30 mx-auto;
  img {
    @apply w-[471px] h-[680px] left-[109px] top-[24px] absolute rounded-[20px] shadow-img;

  }

  .btn_reg {
    @apply  transition w-[300px] h-[60px] px-[26px] py-[5px] left-[651px] top-[380px] absolute bg-black rounded-[30px] border border-white justify-center items-center gap-2.5 inline-flex;
    .text_btn {
      @apply text-center  transition text-white text-xl font-['Roboto'] font-normal ;
    }
  }
  .btn_reg:hover{
    @apply border-red-500 transition duration-500 ;
    .text_btn{
      @apply text-red-500 transition duration-500 scale-110;
    }
  }
  .title_under{
    @apply w-[735px] left-[642px] top-[56px] absolute text-white text-[80px] font-['Roboto'] leading-[94px] font-bold ;
  }
  .desc_under{
    @apply w-[580px] left-[651px] top-[250px] absolute text-white text-lg font-normal font-['Roboto'] leading-loose;
  }
}